.App {
  text-align: center;
  background-color: #00DBDE;
  background-image: linear-gradient(90deg, #00DBDE 0%, #FC00FF 100%); 
  min-height: 100vh;
  font-family: Georgia;
  color: white;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.tuotelista {
  margin-left: 250px;
  margin-top: 50px;
  background-color: #FF7272;
  width: 320px;
  height: auto;
  min-height: 435px;
}

.tilausvahvistus {
  font-size: 23px;
}

.yhteystiedot > input {
  margin-top: 15px;
  width: 200px;
}

.App-header {
  background-color: #51BFE8;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.tiedot {
  flex-direction: row;
  display:flex;
  height:auto;
  width:90%;
  margin-top: 20px;
  margin-left: 10px;
  flex-wrap:wrap;
  min-height: 100px;
}

#ostoskori {
  position: absolute;
  top:23px;
  right: 20px;
}

.App-link {
  color: #61dafb;
}

img {
  height: 300px;
  width: 300px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
